import {Component} from "react";
import {WithProps} from "../utils/props";
import {Link} from "react-router-dom";
import {Button, Col, Form, Input, Layout, message, Row} from "antd";
import {Content} from "antd/es/layout/layout";
import Api from "../utils/api"

class Register extends Component<WithProps, any> {
    constructor(props: WithProps) {
        super(props);
        this.state = {
            is_login: false
        }
    }

    componentDidMount() {
    }

    onFinish = (values: any) => {
        if (values.password !== values.repeatPassword) {
            message.error("")
            return
        } else {
            Api.register(values.username, values.password).then(r => {
                if (r.code !== 200) {
                    message.error(r.error)
                } else {
                    message.info("")
                    this.props.navigate("/login")
                }
            })
        }
    }

    onFinishFailed = (err: any) => {

    }

    render() {
        return <>
            <Layout>
                <Content style={{height: window.innerHeight}}>
                    <Row style={{marginTop: 30}}>
                        <Col offset={10} span={8}>
                            <h1 style={{fontSize: 50, color: "blue"}}></h1>
                        </Col>
                    </Row>
                    <Form
                        style={{marginTop: window.innerHeight * 0.1}}
                        name="basic"
                        labelCol={{span: 8}}
                        wrapperCol={{span: 8}}
                        initialValues={{remember: true}}
                        onFinish={this.onFinish}
                        onFinishFailed={this.onFinishFailed}
                        autoComplete="off"
                    >
                        <Form.Item
                            label=""
                            name="username"
                            rules={[{required: true, message: '!'}]}
                        >
                            <Input placeholder={""}/>
                        </Form.Item>

                        <Form.Item
                            label=""
                            name="password"
                            rules={[{required: true, message: '!'}]}
                        >
                            <Input.Password placeholder={""}/>
                        </Form.Item>

                        <Form.Item
                            label=""
                            name="repeatPassword"
                            rules={[{required: true, message: '!'}]}
                        >
                            <Input.Password placeholder={""}/>
                        </Form.Item>

                        <Form.Item name="remember" valuePropName="checked" wrapperCol={{offset: 8, span: 16}}>
                            <Link to={"/login"}></Link>
                        </Form.Item>

                        <Form.Item wrapperCol={{offset: 11, span: 16}}>
                            <Button type="primary" htmlType="submit">
                                
                            </Button>
                        </Form.Item>


                    </Form>
                </Content>
            </Layout>


        </>;
    }
}


export default Register
